<template>
  <div class="whole-container">
    <h1>토핑 데이터 추가</h1>
    <div class="form-container">
      <form id="cafeForm">
        <div class="form-group">
          <label for="toppingName">카페 이름</label>
          <input
            type="text"
            id="cafeName"
            placeholder="ex. 스타벅스"
            required
            v-model="saveToppingsDto.cafeName"
          />
        </div>
        <div class="form-group">
          <label for="beverageName">음료 이름</label>
          <input
            type="text"
            id="toppingName"
            placeholder="ex. 아이스 아메리카노"
            required
            v-model="saveToppingsDto.beverageName"
          />
        </div>
        <div class="form-group">
          <label for="toppingName">토핑 이름</label>
          <input
            type="text"
            id="toppingName"
            placeholder="ex. 휘핑크림"
            required
            v-model="saveToppingsDto.toppingName"
          />
        </div>

        <div class="form-group">
          <label for="kcal">칼로리 (숫자만 입력)</label>
          <input
            type="number"
            id="kcal"
            placeholder="ex. 165"
            required
            v-model="saveToppingsDto.calorie"
          />
        </div>

        <div class="form-group">
          <label for="sugar">당류 (숫자만 입력)</label>
          <input
            type="number"
            id="sugar"
            placeholder="ex. 30"
            required
            v-model="saveToppingsDto.sugar"
          />
        </div>

        <div class="form-group">
          <label for="manager">담당자</label>
          <input
            type="text"
            id="manager"
            placeholder="ex. 홍길동"
            required
            v-model="saveToppingsDto.manager"
          />
        </div>

        <button type="button" @click="submitForm()">완료</button>
      </form>
    </div>
    <div class="list-container">
      <table class="cafe-list-table">
        <thead>
          <tr>
            <th>ID</th>
            <th>토핑 이름</th>
            <th>당류</th>
            <th>칼로리</th>
            <th>삭제 여부</th>
            <th>카페 이름</th>
            <th>음료 이름</th>
            <th>담당자</th>
            <th>삭제 전환</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(toppings, idx) in storedToppings" :key="toppings">
            <th v-for="topping in toppings" :key="topping">
              {{ topping }}
            </th>
            <button
              class="delete-button"
              @click="deleteTopping(storedToppings[idx].id)"
            >
              삭제 토글
            </button>
          </tr>
        </tbody>
      </table>
      <div v-if="pageCount > 0">
        <div class="btn-cover">
          <button :disabled="pageNum === 0" @click="prevPage" class="page-btn">
            ◀︎
          </button>
          <span class="page-count">{{ pageNum + 1 }} / {{ pageCount }}</span>
          <button
            :disabled="pageNum >= pageCount - 1"
            @click="nextPage"
            class="page-btn"
          >
            ►
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      pageNum: 0,
      storedToppings: [
        {
          toppingId: 0,
          cafeName: "",
          beverageName: "",
          toppingName: "test",
          sugar: 1,
          calorie: 2,
          deleted: false,
          manager: "tm",
        },
      ],
      saveToppingsDto: {
        cafeName: "",
        beverageName: "",
        toppingName: "",
        sugar: "",
        calorie: "",
        deleted: false,
        manager: "",
      },
    };
  },
  props: {
    pageSize: {
      type: Number,
      required: false,
      default: 10,
    },
  },
  components: {},
  mounted() {
    this.loadContents();
  },
  computed: {
    pageCount() {
      let listLeng = this.storedToppings.length,
        listSize = this.pageSize,
        page = Math.floor(listLeng / listSize);
      if (listLeng % listSize > 0) page += 1;
      return page;
    },
    paginatedData() {
      const start = this.pageNum * this.pageSize,
        end = start + this.pageSize;
      return this.storedToppings.slice(start, end);
    },
  },
  methods: {
    nextPage() {
      this.pageNum += 1;
    },
    prevPage() {
      this.pageNum -= 1;
    },
    loadContents() {
      this.$axios.get("/admin/topping").then((res) => {
        this.storedToppings = res.data;
        console.log(res.data);
      });
    },
    submitForm() {
      console.log(this.saveToppingsDto);
      this.$axios
        .post("/admin/topping/save", this.saveToppingsDto)
        .then((res) => {
          this.saveToppingsDto.cafeId = this.storedToppings.length + 1;
          console.log(res);
          this.loadContents();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    deleteTopping(id) {
      console.log(id);
      this.$axios
        .delete("/admin/topping/delete/" + id)
        .then(() => {
          this.loadContents();
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

