import { createWebHistory, createRouter } from "vue-router";
import homePage from "@/components/homePage";
import cafePage from "@/components/cafePage";
import toppingPage from "@/components/toppingPage";
import faqPage from "@/components/FAQPage";
import accountManagePage from "@/components/accountManagePage";
import statisticPage from "@/components/statisticPage";
import announcementPage from "@/components/anouncementPage";
import bmiPage from "@/components/bmiPage";
import diabetesPage from "@/components/diabetesPage";
import statusPage from "@/components/statusPage";
import inquiryPage from "@/components/inquiryPage";
import notificationPage from "@/components/notificationPage";
import diagnosticPage from "@/components/diagnosticPage";

const router = createRouter({
  history: createWebHistory(),
  routes: [
    { path: "/", name: "home", component: homePage },
    { path: "/cafe", name: "cafe", component: cafePage },
    { path: "/topping", name: "topping", component: toppingPage },
    { path: "/faq", name: "faq", component: faqPage },
    { path: "/account", name: "account", component: accountManagePage },
    { path: "/statistic", name: "statistic", component: statisticPage },
    {
      path: "/announcement",
      name: "announcement",
      component: announcementPage,
    },
    { path: "/bmi", name: "bmi", component: bmiPage },
    { path: "/diabetes", name: "diabetes", component: diabetesPage },
    { path: "/status", name: "status", component: statusPage },
    { path: "/inquiry", name: "inquiry", component: inquiryPage },
    {
      path: "/notification",
      name: "notification",
      component: notificationPage,
    },
    {
      path: "/diagnostic",
      name: "diagnostic",
      component: diagnosticPage,
    },
    // {
    //   path: "/:pathMatch(.*)*",
    //   redirect: "/404",
    // },
  ],
});

export default router;
